.app-header {
    background-color: #014e56;
    height: 80vh;
    color: white;
    .header-section {
        height: 100px;
    }
    .section-right {
        gap: 49px;
    }
    .nav-links {
        gap: 49px;
    }
    .logo {
        font-size: 2rem;
        font-weight: bold;
    }
    .contact-btn {
        border-radius: 3rem;
        background-color: white;
        color: #014e56;
        font-weight: bold;
        border: 1px white solid;
        padding: 10px;
    }
    .img-bot {
        height: 100px;
        width: 350px;
        align-self: flex-end;
        position: relative;
        bottom: -124px;
    }
    .hero {
        background-color: #014e56;
        margin-top:-150px;
        .img-left {
            justify-content: flex-start;
            height: 150px;
            width: 150px;
            position: absolute;
            left: -75px;
        }
        .talent {
            font-size: 6rem;
            font-weight: bolder;
            max-width: 35vw;
            text-align: start;
            span {
                color: #f67e7e;
            }
        }
        p {
            max-width: 442px;
            text-align: left;
            font-size: 1.125rem;
        }
        .info {
            img {
            }
        }
    }
}
